@import url(https://fonts.googleapis.com/css?family=Open+Sans);
/* App main front */
body {
  font-family: "Lato", "Helvetica Neue", Helvetica, Arial, sans-serif;
}
/* Primary font - Paragraghs */
p {
  font-family: "Open Sans", sans-serif;
}
/* Secondary Font - Headings */
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Lato", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: 700;
}

/*
Primary-color: #1c3e59 - Dark
Secondary-color: #468fc9 
Background-color: #f0f0f0
Extra-Color: #dda222 - Yellow
*/

/* Customising Nav */
.navbar {
  background-color: #f0f0f0;
}
.navbar-light .navbar-nav .nav-link {
  color: #1c3e59 !important;
}

/* Buttons */
button,
input[type="submit"],
.button {
  background: #468fc9;
  color: #ffffff;
  border-radius: 0 !important;
}

.form-control,
.form-control-lg,
.input-group {
  border-radius: 0 !important;
}

.card-height {
  height: 250px;
}
.card-height > a {
  position: absolute;
  right: 0;
  bottom: 0;
}

/*!
 * Start Bootstrap - Landing Page v5.0.4 (https://startbootstrap.com/template-overviews/landing-page)
 * Copyright 2013-2019 Start Bootstrap
 * Licensed under MIT (https://github.com/BlackrockDigital/startbootstrap-landing-page/blob/master/LICENSE)
 */

header.masthead {
  /* position excluded to make z-index work. If encounting probblems uncomment*/
  position: relative;
  background-color: #343a40;
  background: url(/rank-finder/static/media/city-aerial-view.e3c5643c.jpg) no-repeat center center;
  background-size: cover;
  height: 100%;
  /* margin-top: 56px; */
  padding-top: 12rem;
  padding-bottom: 8rem;
  /* z-index: 1; */
  text-align: center;
}

header.masthead .overlay {
  position: absolute;
  background-color: #1c3e59;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  opacity: 0.7;
}

header.masthead h1 {
  font-size: 2rem;
}

@media (min-width: 768px) {
  header.masthead {
    padding-top: 12rem;
    padding-bottom: 12rem;
  }
  header.masthead h1 {
    font-size: 3rem;
  }
}

.showcase .showcase-text {
  padding: 3rem;
}

.showcase .showcase-img {
  min-height: 30rem;
  background-size: cover;
}

@media (min-width: 768px) {
  .showcase .showcase-text {
    padding: 7rem;
  }
}

.features-icons {
  padding-top: 7rem;
  padding-bottom: 7rem;
}

.features-icons .features-icons-item {
  max-width: 20rem;
}

.features-icons .features-icons-item .features-icons-icon {
  height: 7rem;
}

.features-icons .features-icons-item .features-icons-icon i {
  font-size: 4.5rem;
}

.features-icons .features-icons-item:hover .features-icons-icon i {
  font-size: 5rem;
}

.testimonials {
  padding-top: 7rem;
  padding-bottom: 7rem;
}

.testimonials .testimonial-item {
  max-width: 18rem;
}

.testimonials .testimonial-item img {
  max-width: 12rem;
  box-shadow: 0px 5px 5px 0px #adb5bd;
}

/* About background colour */
.bg-image {
  background-image: url(/rank-finder/static/media/SOUTH-AFRICA-TAXIS.fd60ade8.jpg);
  background-size: cover;
  background-position: center;
  height: 600px;
}

/* Foot Style */
.footer-body {
  background: rgb(27, 56, 80);
}
.footer-copyright {
  background: #1c3e59;
}

